import React from "react";
import Base from "terra-base";
import Heading from "terra-heading";
// import SearchSelectField from "./terra-form-select/src/SearchSelectField";
// import MultiSelectField from "./terra-form-select/src/MultiSelectField";
import SearchSelectField from "terra-form-select/lib/SearchSelectField";
import MultiSelectField from "terra-form-select/lib/MultiSelectField";
import "./App.css";
import doctors from "./doctors";

function App() {
  return (
    <Base locale="en">
      <div className="App">
        <form>
          <Heading level={1}>Select Accessibility Tests</Heading>
          <SearchSelectField
            isTouchAccessible
            allowClear
            placeholder="Select a recipient"
            required
            label="To (single-select):"
            selectId="single-search-select-recipients"
          >
            {doctors.map(doctor => (
              <SearchSelectField.Option
                key={doctor}
                value={doctor}
                display={doctor}
              />
            ))}
          </SearchSelectField>
          <MultiSelectField
            isTouchAccessible
            required
            placeholder="Select a recipient"
            label="To (multi-select):"
            selectId="multi-select-recipients"
          >
            {doctors.map(doctor => (
              <MultiSelectField.Option
                key={doctor}
                value={doctor}
                display={doctor}
              />
            ))}
          </MultiSelectField>
        </form>
      </div>
    </Base>
  );
}

export default App;
